import logmain_Js from "../../common/log/logmainjs.js";
var _this;
export default {
	data() {
		return {
			showPassword: false,
			captchaPath: '', //验证码
			captchaPathSh: true, //手动获取验证码

			showAgree: false, //协议是否选择
			isRotate: false, //是否加载旋转
			isRequest: false, //验证码是否旋转
			ruleForm: {
				username: '',
				password: '',
				captcha: '', //验证码
				uuid: '', //生成验证码对应的 id
			},
		}
	},

	mounted() {
		_this = this;
	},
	onReady() {
		_this.getVerCode();
		_this.isRequest = false; //后端没有返回验证码前 点击不了
	},
	methods: {
		display() {
			this.showPassword = !this.showPassword
		},

		async getVerCode() {
			//获取验证码
			if (this.isRequest) {
				//判断是否加载中，避免重复点击请求
				return false;
			}
			this.isRequest = true;
			//获取验证码
			this.ruleForm.uuid = this.$http.getUuid();
			let uuidform = {
				uuid: this.ruleForm.uuid
			};
			let [err, res] = await this.$http.getV2(this.$urlconfig.logCaptcha, uuidform, {});
			this.isRequest = false;
			if (!logmain_Js.errorCheckRe(err, true)) {
				this.captchaPathSh = false;
				return;
			}
			this.captchaPathSh = true;
			this.captchaPath = res.data.msg
		},
		async submitForm() {
			//登陆
			if (this.isRotate) {
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (this.ruleForm.username.length > 18 || this.ruleForm.username.length < 8) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '用户名在8-18位之间'
				});
				return false;
			}

			if (this.ruleForm.password.length > 18 || this.ruleForm.password.length < 8) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '密码在8-18位之间'
				});
				return false;
			}
			if (this.ruleForm.captcha.length != 4) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '验证码不正确'
				});
				return false;
			}
			this.isRotate = true;
			let [err, res] = await this.$http.postV2(this.$urlconfig.logLogin, this.ruleForm, {});
			this.isRotate = false;
			if (!logmain_Js.errorCheckRe(err)) {
				return;
			}
			if (!logmain_Js.checkLog(res)) {
				this.getVerCode();
				return;
			}

		}
	}
}
